<template>
  <v-content>
    <v-container fluid fill-height>
      <v-layout align-center justify-center>
        <v-flex xs12 sm8 md4>
          <v-card class="elevation-12">
            <v-toolbar dark color="primary">
              <v-toolbar-title> </v-toolbar-title>
              <v-spacer></v-spacer>
            </v-toolbar>
            <v-card-text>
              <v-form>
                <v-text-field prepend-icon="person" name="login"
                              label="Login" type="text" v-model="username">

                </v-text-field>
                <v-text-field id="password" prepend-icon="lock" name="password"
                              label="Password" type="password" v-model="password"
                              @keyup.enter.native="clickLogin">

                </v-text-field>
              </v-form>
            </v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="primary" @click="clickLogin">Login</v-btn>
            </v-card-actions>
          </v-card>
        </v-flex>
      </v-layout>
    </v-container>
  </v-content>
</template>
<script>
import { mapMutations, mapActions } from 'vuex'

export default {
  name: 'login',
  components: {
  },
  created () {
    //  
    //   
  },
  props: {
  },
  data () {
    return {
      username: '',
      password: ''
    }
  },
  computed: {
  },
  methods: {
    ...mapMutations([
      'setShowCommonMenu'
    ]),
    ...mapActions([
      'login'
    ]),
    clickLogin () {
      const username = this.username.trim()
      const password = this.password.trim()

      if (username === '') {
        alert(' ')
        return
      }
      if (password === '') {
        alert(' .')
        return
      }

      const user = {
        username,
        password
      }
      this.login(user)
      this.initValue()
    },
    initValue () {
      this.username = ''
      this.password = ''
    }
  },
  destroyed () {
    //   
    //  
  }
}
</script>



